@font-face {
  font-family: "SourceSerif";
  src: url("../../fonts/SourceSerif4_36pt-ExtraBold.ttf") format("woff");
}

.employee__agreement-display {
  margin: 0 auto;
  margin-top: 35px;
  max-width: 1000px;
}

.employee__agreement-display__chart {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-bottom: 30px;
  background-color: #fff;
  border: 4px solid black;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

.column {
  width: 50%;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
}

.column__label {
  font-family: "SourceSerif";
  font-size: 28px;
  color: black;
  margin: 0;
  text-align: center;
  border-bottom: 4px solid black;
  padding-bottom: 10px;
}

.column__employee-name {
  font-family: "SourceSerif";
  font-size: 30px;
  margin: 0;
  margin: auto 0;
  border-bottom: 3px solid black;
  height: 100px;
  text-overflow: ellipsis;
  text-align: center;
  overflow: hidden;
  line-height: 1.2;
  white-space: nowrap;
  padding: 30px 20px 20px 20px;
}

.column__employee-signature {
  height: 100px;
  width: 100%;
  padding: 10px;
  border-bottom: 3px solid black;
}


@media  screen and (max-width: 560px){
  .column__label {
    font-size: 22px;
    padding: 0;
  }
  .column__employee-name {
    font-size: 16px;
  }

  .column__employee-signature {
    padding: 5px
  }
  
}

@media screen and (min-width:561px) and (max-width:800px){
  .column__employee-name{
    font-size: 24px;
  }
}